.app-wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  display: grid;
  grid-template-areas:
  "h h"
  "n c";
  grid-template-rows: 94px 1fr;
  grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
  grid-area: c;
}

@media (max-width: 768px)  {
  .app-wrapper {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0;
    display: grid;
    grid-template-areas:
    "h h"
    "c c";
    grid-template-rows: 94px 1fr;
    grid-template-columns: 2fr 10fr;
  }
}





